import React, { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useParams, useNavigate } from 'react-router-dom'
import { 
  BookOpen, 
  ChevronLeft, 
  ChevronRight, 
  CheckCircle, 
  Play,
  Search,
  Globe,
  Shield,
  User,
  Eye,
  Target
} from 'lucide-react'
import './LessonViewer.css'

const LessonViewer = () => {
  const { lessonId } = useParams()
  const navigate = useNavigate()
  const [currentSlide, setCurrentSlide] = useState(0)
  const [isCompleted, setIsCompleted] = useState(false)
  const [progress, setProgress] = useState(0)

  // Contenido de las lecciones
  const lessons = {
    intro: {
      title: "¿Qué es OSINT?",
      description: "Introducción al mundo de la inteligencia de fuentes abiertas",
      totalSlides: 8,
      slides: [
        {
          id: 1,
          title: "¿Qué es OSINT?",
          content: `
            <h2>Open Source Intelligence</h2>
            <p><strong>OSINT</strong> significa <em>Open Source Intelligence</em> o Inteligencia de Fuentes Abiertas.</p>
            <p>Es la disciplina que se encarga de recopilar, analizar y utilizar información que está disponible públicamente.</p>
            <div class="highlight-box">
              <p>💡 <strong>Dato clave:</strong> No requiere acceso privilegiado o métodos de hacking</p>
            </div>
          `,
          interactive: {
            type: 'icons',
            items: [
              { icon: Search, label: 'Búsqueda' },
              { icon: Eye, label: 'Observación' },
              { icon: Globe, label: 'Internet' },
              { icon: BookOpen, label: 'Análisis' }
            ]
          }
        },
        {
          id: 2,
          title: "Fuentes de Información",
          content: `
            <h2>¿De dónde viene la información?</h2>
            <p>Las fuentes OSINT incluyen cualquier información disponible públicamente:</p>
            <ul>
              <li><strong>🌐 Internet:</strong> Sitios web, blogs, foros</li>
              <li><strong>📱 Redes Sociales:</strong> Facebook, Twitter, Instagram, LinkedIn</li>
              <li><strong>📰 Medios:</strong> Periódicos, revistas, noticias online</li>
              <li><strong>📚 Académico:</strong> Papers, investigaciones, tesis</li>
              <li><strong>🏛️ Gobierno:</strong> Registros públicos, documentos oficiales</li>
            </ul>
          `,
          interactive: {
            type: 'progress_bar',
            items: [
              { label: 'Internet', percentage: 40 },
              { label: 'Redes Sociales', percentage: 25 },
              { label: 'Medios', percentage: 20 },
              { label: 'Otros', percentage: 15 }
            ]
          }
        },
        {
          id: 3,
          title: "Aplicaciones de OSINT",
          content: `
            <h2>¿Para qué se usa OSINT?</h2>
            <div class="applications-grid">
              <div class="app-card">
                <h3>🔒 Ciberseguridad</h3>
                <p>Identificación de amenazas y vulnerabilidades</p>
              </div>
              <div class="app-card">
                <h3>📰 Periodismo</h3>
                <p>Investigación y verificación de noticias</p>
              </div>
              <div class="app-card">
                <h3>🏢 Empresarial</h3>
                <p>Investigación de competencia y mercado</p>
              </div>
              <div class="app-card">
                <h3>⚖️ Legal</h3>
                <p>Búsqueda de evidencias y antecedentes</p>
              </div>
            </div>
          `,
          interactive: {
            type: 'click_reveal',
            items: [
              { trigger: 'Ciberseguridad', content: 'Detectar campañas de phishing, perfiles falsos, y amenazas emergentes' },
              { trigger: 'Periodismo', content: 'Verificar fuentes, investigar historias, fact-checking' },
              { trigger: 'Empresarial', content: 'Análisis de competencia, due diligence, investigación de mercado' },
              { trigger: 'Legal', content: 'Localizar testigos, verificar testimonios, buscar evidencias digitales' }
            ]
          }
        },
        {
          id: 4,
          title: "Herramientas Básicas",
          content: `
            <h2>Herramientas Fundamentales</h2>
            <p>Empezamos con las herramientas más básicas pero poderosas:</p>
            <div class="tools-showcase">
              <div class="tool-item">
                <strong>🔍 Google Search</strong>
                <p>El motor de búsqueda más potente con operadores avanzados</p>
              </div>
              <div class="tool-item">
                <strong>🖼️ Google Images</strong>
                <p>Búsqueda reversa de imágenes para verificación</p>
              </div>
              <div class="tool-item">
                <strong>🗺️ Google Maps</strong>
                <p>Geolocalización y análisis de ubicaciones</p>
              </div>
              <div class="tool-item">
                <strong>📱 Redes Sociales</strong>
                <p>Búsquedas avanzadas en plataformas sociales</p>
              </div>
            </div>
          `,
          interactive: {
            type: 'tool_demo',
            tools: ['google', 'images', 'maps', 'social']
          }
        },
        {
          id: 5,
          title: "Google Dorks",
          content: `
            <h2>Búsquedas Avanzadas con Google</h2>
            <p>Los <strong>Google Dorks</strong> son consultas especializadas que nos permiten encontrar información específica:</p>
            <div class="dorks-examples">
              <div class="dork-example">
                <code>site:ejemplo.com</code>
                <p>Busca solo en el sitio especificado</p>
              </div>
              <div class="dork-example">
                <code>filetype:pdf</code>
                <p>Busca solo archivos PDF</p>
              </div>
              <div class="dork-example">
                <code>intitle:"login"</code>
                <p>Busca páginas con "login" en el título</p>
              </div>
            </div>
          `,
          interactive: {
            type: 'dork_builder',
            examples: [
              'site:github.com "password"',
              'filetype:xlsx "budget 2024"',
              'intitle:"index of" filetype:pdf'
            ]
          }
        },
        {
          id: 6,
          title: "Ética en OSINT",
          content: `
            <h2>Consideraciones Éticas</h2>
            <p>Es fundamental usar OSINT de manera responsable:</p>
            <div class="ethics-rules">
              <div class="rule-item good">
                <h3>✅ Hacer</h3>
                <ul>
                  <li>Usar solo información pública</li>
                  <li>Respetar la privacidad</li>
                  <li>Verificar la información</li>
                  <li>Actuar dentro de la ley</li>
                </ul>
              </div>
              <div class="rule-item bad">
                <h3>❌ No Hacer</h3>
                <ul>
                  <li>Acceder a cuentas privadas</li>
                  <li>Hacer ingeniería social</li>
                  <li>Acosar o intimidar</li>
                  <li>Usar información maliciosamente</li>
                </ul>
              </div>
            </div>
          `,
          interactive: {
            type: 'ethics_quiz',
            questions: [
              {
                question: "¿Es ético buscar información pública en redes sociales?",
                correct: "Sí, si es información pública",
                options: ["Sí, si es información pública", "No, nunca", "Solo con permiso"]
              }
            ]
          }
        },
        {
          id: 7,
          title: "Metodología OSINT",
          content: `
            <h2>Proceso de Investigación OSINT</h2>
            <p>Un enfoque sistemático para investigaciones efectivas:</p>
            <div class="methodology-steps">
              <div class="step">
                <span class="step-number">1</span>
                <h3>Definir Objetivos</h3>
                <p>¿Qué información necesitas encontrar?</p>
              </div>
              <div class="step">
                <span class="step-number">2</span>
                <h3>Identificar Fuentes</h3>
                <p>¿Dónde es probable que esté la información?</p>
              </div>
              <div class="step">
                <span class="step-number">3</span>
                <h3>Recopilar Datos</h3>
                <p>Búsqueda sistemática y documentación</p>
              </div>
              <div class="step">
                <span class="step-number">4</span>
                <h3>Analizar y Verificar</h3>
                <p>Evaluar la credibilidad y relevancia</p>
              </div>
              <div class="step">
                <span class="step-number">5</span>
                <h3>Documentar Resultados</h3>
                <p>Crear un informe estructurado</p>
              </div>
            </div>
          `,
          interactive: {
            type: 'methodology_flow',
            steps: 5
          }
        },
        {
          id: 8,
          title: "¡Felicidades!",
          content: `
            <h2>🎉 ¡Has completado la introducción a OSINT!</h2>
            <p>Ahora tienes los conocimientos básicos sobre:</p>
            <div class="completion-summary">
              <div class="summary-item">
                <CheckCircle size={24} />
                <span>Qué es OSINT y sus aplicaciones</span>
              </div>
              <div class="summary-item">
                <CheckCircle size={24} />
                <span>Herramientas básicas de investigación</span>
              </div>
              <div class="summary-item">
                <CheckCircle size={24} />
                <span>Google Dorks para búsquedas avanzadas</span>
              </div>
              <div class="summary-item">
                <CheckCircle size={24} />
                <span>Consideraciones éticas importantes</span>
              </div>
              <div class="summary-item">
                <CheckCircle size={24} />
                <span>Metodología de investigación</span>
              </div>
            </div>
            <div class="next-steps">
              <h3>Próximos pasos:</h3>
              <button class="action-button" onclick="window.location.href='/academy/dork-simulator'">
                <Target size={20} />
                Practicar con el Simulador de Dorks
              </button>
              <button class="action-button" onclick="window.location.href='/academy/detective-game'">
                <Play size={20} />
                Jugar Detective Digital
              </button>
            </div>
          `,
          interactive: {
            type: 'completion',
            actions: ['simulator', 'game', 'mindmap']
          }
        }
      ]
    },
    tools: {
      title: "Herramientas Básicas",
      description: "Conoce las herramientas esenciales para OSINT",
      totalSlides: 6,
      slides: [
        // Más lecciones aquí...
      ]
    }
  }

  const currentLesson = lessons[lessonId]

  useEffect(() => {
    if (currentLesson) {
      setProgress((currentSlide + 1) / currentLesson.totalSlides * 100)
    }
  }, [currentSlide, currentLesson])

  const handleNext = () => {
    if (currentSlide < currentLesson.totalSlides - 1) {
      setCurrentSlide(currentSlide + 1)
    } else {
      setIsCompleted(true)
    }
  }

  const handlePrevious = () => {
    if (currentSlide > 0) {
      setCurrentSlide(currentSlide - 1)
    }
  }

  const handleBackToAcademy = () => {
    navigate('/academy')
  }

  if (!currentLesson) {
    return (
      <div className="lesson-error">
        <h2>Lección no encontrada</h2>
        <button onClick={handleBackToAcademy}>Volver a la Academia</button>
      </div>
    )
  }

  const currentSlideData = currentLesson.slides[currentSlide]

  return (
    <div className="lesson-viewer">
      <div className="lesson-header">
        <button onClick={handleBackToAcademy} className="back-button">
          <ChevronLeft size={20} />
          Volver a la Academia
        </button>
        <div className="lesson-info">
          <h1>{currentLesson.title}</h1>
          <p>{currentLesson.description}</p>
        </div>
        <div className="lesson-progress">
          <span>{currentSlide + 1} / {currentLesson.totalSlides}</span>
          <div className="progress-bar">
            <div 
              className="progress-fill"
              style={{ width: `${progress}%` }}
            />
          </div>
        </div>
      </div>

      <div className="lesson-content">
        <AnimatePresence mode="wait">
          <motion.div
            key={currentSlide}
            className="slide"
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -50 }}
            transition={{ duration: 0.3 }}
          >
            <div className="slide-content">
              <div 
                className="slide-text"
                dangerouslySetInnerHTML={{ __html: currentSlideData.content }}
              />
              
              {/* Componente interactivo según el tipo */}
              {currentSlideData.interactive && (
                <div className="interactive-section">
                  {renderInteractiveComponent(currentSlideData.interactive)}
                </div>
              )}
            </div>
          </motion.div>
        </AnimatePresence>
      </div>

      <div className="lesson-navigation">
        <button 
          onClick={handlePrevious}
          disabled={currentSlide === 0}
          className="nav-button prev"
        >
          <ChevronLeft size={20} />
          Anterior
        </button>
        
        <div className="slide-indicators">
          {Array.from({ length: currentLesson.totalSlides }).map((_, index) => (
            <button
              key={index}
              className={`indicator ${index === currentSlide ? 'active' : ''} ${index < currentSlide ? 'completed' : ''}`}
              onClick={() => setCurrentSlide(index)}
            />
          ))}
        </div>

        <button 
          onClick={handleNext}
          className="nav-button next"
        >
          {currentSlide === currentLesson.totalSlides - 1 ? 'Finalizar' : 'Siguiente'}
          <ChevronRight size={20} />
        </button>
      </div>
    </div>
  )

  function renderInteractiveComponent(interactive) {
    switch (interactive.type) {
      case 'icons':
        return (
          <div className="icons-showcase">
            {interactive.items.map((item, index) => (
              <motion.div
                key={index}
                className="icon-item"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
              >
                <item.icon size={48} />
                <span>{item.label}</span>
              </motion.div>
            ))}
          </div>
        )
      
      case 'progress_bar':
        return (
          <div className="stats-bars">
            {interactive.items.map((item, index) => (
              <div key={index} className="stat-bar">
                <div className="stat-label">
                  <span>{item.label}</span>
                  <span>{item.percentage}%</span>
                </div>
                <div className="bar">
                  <motion.div
                    className="bar-fill"
                    initial={{ width: 0 }}
                    animate={{ width: `${item.percentage}%` }}
                    transition={{ delay: index * 0.2, duration: 0.8 }}
                  />
                </div>
              </div>
            ))}
          </div>
        )
      
      default:
        return null
    }
  }
}

export default LessonViewer