import React, { useState, useEffect, useRef } from 'react'
import { 
  Play, 
  Pause, 
  RotateCcw, 
  Trophy,
  Clock,
  Search,
  AlertCircle,
  CheckCircle
} from 'lucide-react'
import './DetectiveGame.css'

const DetectiveGame = () => {
  const canvasRef = useRef(null)
  const [gameState, setGameState] = useState('menu') // menu, playing, paused, completed
  const [currentCase, setCurrentCase] = useState(0)
  const [score, setScore] = useState(0)
  const [timeLeft, setTimeLeft] = useState(300) // 5 minutos por caso
  const [hints, setHints] = useState(3)
  const [foundClues, setFoundClues] = useState([])
  const [selectedTool, setSelectedTool] = useState('search')

  // Casos del juego
  const cases = [
    {
      id: 1,
      title: 'El Perfil Perdido',
      description: 'Un usuario reportó que su perfil de redes sociales fue suplantado. Encuentra evidencia del perfil falso.',
      scenario: 'socialMedia',
      clues: [
        { id: 'profile_pic', x: 150, y: 200, radius: 30, points: 100, hint: 'Compara la foto de perfil' },
        { id: 'username', x: 300, y: 150, radius: 25, points: 150, hint: 'El username tiene un carácter extraño' },
        { id: 'post_date', x: 450, y: 300, radius: 20, points: 200, hint: 'Revisa las fechas de publicación' }
      ],
      tools: ['search', 'compare', 'timeline']
    },
    {
      id: 2,
      title: 'La Imagen Modificada',
      description: 'Una imagen viral parece ser falsa. Usa herramientas OSINT para verificar su autenticidad.',
      scenario: 'imageAnalysis',
      clues: [
        { id: 'metadata', x: 200, y: 150, radius: 35, points: 200, hint: 'Los metadatos revelan la verdad' },
        { id: 'shadows', x: 350, y: 280, radius: 30, points: 150, hint: 'Las sombras no coinciden' },
        { id: 'reverse_search', x: 500, y: 200, radius: 25, points: 250, hint: 'Búsqueda inversa de imagen' }
      ],
      tools: ['metadata', 'reverse_search', 'pixel_analysis']
    },
    {
      id: 3,
      title: 'El Email Sospechoso',
      description: 'Verifica si un email es legítimo o un intento de phishing usando técnicas OSINT.',
      scenario: 'emailVerification',
      clues: [
        { id: 'sender_domain', x: 250, y: 100, radius: 30, points: 300, hint: 'Verifica el dominio del remitente' },
        { id: 'headers', x: 400, y: 200, radius: 35, points: 250, hint: 'Los headers del email tienen información clave' },
        { id: 'links', x: 300, y: 350, radius: 25, points: 200, hint: 'Analiza los enlaces sin hacer clic' }
      ],
      tools: ['whois', 'header_analysis', 'link_preview']
    }
  ]

  // Timer del juego
  useEffect(() => {
    let timer
    if (gameState === 'playing' && timeLeft > 0) {
      timer = setInterval(() => {
        setTimeLeft(prev => prev - 1)
      }, 1000)
    } else if (timeLeft === 0) {
      handleCaseComplete(false)
    }
    return () => clearInterval(timer)
  }, [gameState, timeLeft])

  // Inicializar canvas
  useEffect(() => {
    if (canvasRef.current && gameState === 'playing') {
      drawScene()
    }
  }, [gameState, currentCase, foundClues])

  const drawScene = () => {
    const canvas = canvasRef.current
    const ctx = canvas.getContext('2d')
    
    // Limpiar canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height)
    
    // Dibujar fondo según el escenario
    drawBackground(ctx, cases[currentCase].scenario)
    
    // Dibujar pistas (solo las no encontradas)
    cases[currentCase].clues.forEach(clue => {
      if (!foundClues.includes(clue.id)) {
        drawClue(ctx, clue)
      }
    })
    
    // Dibujar pistas encontradas con efecto
    foundClues.forEach(clueId => {
      const clue = cases[currentCase].clues.find(c => c.id === clueId)
      if (clue) {
        drawFoundClue(ctx, clue)
      }
    })
  }

  const drawBackground = (ctx, scenario) => {
    const { width, height } = ctx.canvas
    
    // Gradiente de fondo
    const gradient = ctx.createLinearGradient(0, 0, width, height)
    gradient.addColorStop(0, '#1a1a2e')
    gradient.addColorStop(1, '#16213e')
    ctx.fillStyle = gradient
    ctx.fillRect(0, 0, width, height)
    
    // Elementos del escenario
    ctx.strokeStyle = '#0f4c75'
    ctx.lineWidth = 2
    
    switch(scenario) {
      case 'socialMedia':
        // Dibujar interfaz de red social simplificada
        ctx.fillStyle = '#2c3e50'
        ctx.fillRect(50, 50, width - 100, 80)
        ctx.fillStyle = '#34495e'
        ctx.fillRect(50, 150, width - 100, height - 200)
        break
      case 'imageAnalysis':
        // Dibujar marco de imagen
        ctx.strokeRect(100, 50, width - 200, height - 100)
        break
      case 'emailVerification':
        // Dibujar interfaz de email
        ctx.fillStyle = '#2c3e50'
        ctx.fillRect(50, 50, width - 100, 60)
        ctx.fillRect(50, 130, width - 100, height - 180)
        break
    }
  }

  const drawClue = (ctx, clue) => {
    // Efecto de pulsación para pistas ocultas
    const pulse = Math.sin(Date.now() * 0.002) * 5
    
    ctx.beginPath()
    ctx.arc(clue.x, clue.y, clue.radius + pulse, 0, Math.PI * 2)
    ctx.fillStyle = 'rgba(52, 152, 219, 0.1)'
    ctx.fill()
    ctx.strokeStyle = 'rgba(52, 152, 219, 0.3)'
    ctx.stroke()
  }

  const drawFoundClue = (ctx, clue) => {
    // Pista encontrada con check
    ctx.beginPath()
    ctx.arc(clue.x, clue.y, clue.radius, 0, Math.PI * 2)
    ctx.fillStyle = 'rgba(46, 204, 113, 0.3)'
    ctx.fill()
    ctx.strokeStyle = '#2ecc71'
    ctx.lineWidth = 3
    ctx.stroke()
    
    // Dibujar checkmark
    ctx.strokeStyle = '#fff'
    ctx.lineWidth = 2
    ctx.beginPath()
    ctx.moveTo(clue.x - 10, clue.y)
    ctx.lineTo(clue.x - 3, clue.y + 7)
    ctx.lineTo(clue.x + 10, clue.y - 7)
    ctx.stroke()
  }

  const handleCanvasClick = (e) => {
    if (gameState !== 'playing') return
    
    const canvas = canvasRef.current
    const rect = canvas.getBoundingClientRect()
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top
    
    // Verificar si se hizo clic en una pista
    const currentCaseData = cases[currentCase]
    currentCaseData.clues.forEach(clue => {
      if (!foundClues.includes(clue.id)) {
        const distance = Math.sqrt((x - clue.x) ** 2 + (y - clue.y) ** 2)
        if (distance <= clue.radius) {
          handleClueFound(clue)
        }
      }
    })
  }

  const handleClueFound = (clue) => {
    setFoundClues([...foundClues, clue.id])
    setScore(score + clue.points)
    
    // Verificar si se encontraron todas las pistas
    if (foundClues.length + 1 === cases[currentCase].clues.length) {
      handleCaseComplete(true)
    }
  }

  const handleCaseComplete = (success) => {
    setGameState('completed')
    
    if (success) {
      // Bonus por tiempo restante
      const timeBonus = Math.floor(timeLeft / 10) * 10
      setScore(score + timeBonus)
    }
  }

  const startGame = () => {
    setGameState('playing')
    setCurrentCase(0)
    setScore(0)
    setTimeLeft(300)
    setHints(3)
    setFoundClues([])
  }

  const nextCase = () => {
    if (currentCase < cases.length - 1) {
      setCurrentCase(currentCase + 1)
      setTimeLeft(300)
      setFoundClues([])
      setGameState('playing')
    } else {
      // Juego completado
      setGameState('menu')
    }
  }

  const useHint = () => {
    if (hints > 0) {
      setHints(hints - 1)
      // Mostrar pista de una pista no encontrada
      const unFoundClues = cases[currentCase].clues.filter(c => !foundClues.includes(c.id))
      if (unFoundClues.length > 0) {
        alert(`Pista: ${unFoundClues[0].hint}`)
      }
    }
  }

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
  }

  return (
    <div className="detective-game">
      <div className="game-header">
        <h1>Detective Digital OSINT</h1>
        {gameState === 'playing' && (
          <div className="game-stats">
            <div className="stat">
              <Trophy size={20} />
              <span>{score} pts</span>
            </div>
            <div className="stat">
              <Clock size={20} />
              <span>{formatTime(timeLeft)}</span>
            </div>
            <div className="stat">
              <AlertCircle size={20} />
              <span>{hints} pistas</span>
            </div>
          </div>
        )}
      </div>

      {gameState === 'menu' && (
        <div className="game-menu">
          <h2>¡Bienvenido al Juego del Detective Digital!</h2>
          <p>
            Usa tus habilidades OSINT para resolver casos reales. 
            Encuentra pistas ocultas, verifica información y conviértete 
            en un experto investigador digital.
          </p>
          <div className="menu-features">
            <div className="feature">
              <Search size={32} />
              <h3>Investiga</h3>
              <p>Busca pistas en diferentes escenarios</p>
            </div>
            <div className="feature">
              <Clock size={32} />
              <h3>Contra el Tiempo</h3>
              <p>Resuelve casos antes de que se acabe el tiempo</p>
            </div>
            <div className="feature">
              <Trophy size={32} />
              <h3>Gana Puntos</h3>
              <p>Mejora tu puntuación y desbloquea nuevos casos</p>
            </div>
          </div>
          <button className="start-button" onClick={startGame}>
            <Play size={20} />
            Comenzar Juego
          </button>
        </div>
      )}

      {gameState === 'playing' && (
        <div className="game-area">
          <div className="case-info">
            <h3>Caso {currentCase + 1}: {cases[currentCase].title}</h3>
            <p>{cases[currentCase].description}</p>
            <div className="progress">
              <span>Pistas encontradas: {foundClues.length}/{cases[currentCase].clues.length}</span>
            </div>
          </div>

          <div className="game-canvas-container">
            <canvas
              ref={canvasRef}
              width={700}
              height={400}
              onClick={handleCanvasClick}
              className="game-canvas"
            />
          </div>

          <div className="game-tools">
            <button 
              className="tool-button"
              onClick={useHint}
              disabled={hints === 0}
            >
              <AlertCircle size={16} />
              Usar Pista ({hints})
            </button>
            <button 
              className="tool-button"
              onClick={() => setGameState('paused')}
            >
              <Pause size={16} />
              Pausar
            </button>
          </div>
        </div>
      )}

      {gameState === 'completed' && (
        <div className="game-completed">
          <h2>¡Caso Resuelto!</h2>
          <div className="completion-stats">
            <div className="stat-card">
              <Trophy size={32} />
              <h3>Puntuación Total</h3>
              <p className="score-number">{score}</p>
            </div>
            <div className="stat-card">
              <Clock size={32} />
              <h3>Tiempo Restante</h3>
              <p>{formatTime(timeLeft)}</p>
            </div>
            <div className="stat-card">
              <CheckCircle size={32} />
              <h3>Pistas Encontradas</h3>
              <p>{foundClues.length}/{cases[currentCase].clues.length}</p>
            </div>
          </div>
          
          <div className="completion-actions">
            {currentCase < cases.length - 1 ? (
              <button className="next-button" onClick={nextCase}>
                Siguiente Caso
              </button>
            ) : (
              <button className="menu-button" onClick={() => setGameState('menu')}>
                Volver al Menú
              </button>
            )}
          </div>
        </div>
      )}

      {gameState === 'paused' && (
        <div className="game-paused">
          <h2>Juego Pausado</h2>
          <button 
            className="resume-button"
            onClick={() => setGameState('playing')}
          >
            <Play size={20} />
            Continuar
          </button>
          <button 
            className="menu-button"
            onClick={() => setGameState('menu')}
          >
            <RotateCcw size={20} />
            Volver al Menú
          </button>
        </div>
      )}
    </div>
  )
}

export default DetectiveGame