import React, { useState, useEffect } from 'react'
import { 
  BookOpen, 
  Trophy, 
  Target, 
  Brain, 
  Search, 
  Award,
  TrendingUp,
  Clock,
  Lock,
  Unlock
} from 'lucide-react'
import { useNavigate } from 'react-router-dom'
import './AcademyDashboard.css'

const AcademyDashboard = () => {
  const navigate = useNavigate()
  const [userProgress, setUserProgress] = useState({
    level: 1,
    xp: 0,
    completedLessons: [],
    achievements: [],
    currentStreak: 0
  })

  const modules = [
    {
      id: 'intro',
      title: '¿Qué es OSINT?',
      description: 'Introducción al mundo de la inteligencia de fuentes abiertas',
      icon: BookOpen,
      duration: '15 min',
      difficulty: 'Principiante',
      xpReward: 100,
      locked: false,
      progress: 0
    },
    {
      id: 'tools',
      title: 'Herramientas Básicas',
      description: 'Conoce las herramientas esenciales para OSINT',
      icon: Target,
      duration: '20 min',
      difficulty: 'Principiante',
      xpReward: 150,
      locked: false,
      progress: 0
    },
    {
      id: 'techniques',
      title: 'Técnicas de Búsqueda',
      description: 'Domina Google Dorks y búsquedas avanzadas',
      icon: Search,
      duration: '30 min',
      difficulty: 'Intermedio',
      xpReward: 200,
      locked: true,
      progress: 0
    },
    {
      id: 'mindmap',
      title: 'Mapa Mental OSINT',
      description: 'Explora las conexiones entre diferentes técnicas',
      icon: Brain,
      duration: '25 min',
      difficulty: 'Intermedio',
      xpReward: 250,
      locked: true,
      progress: 0
    },
    {
      id: 'detective',
      title: 'Detective Digital',
      description: 'Juego interactivo: Resuelve casos usando OSINT',
      icon: Trophy,
      duration: '45 min',
      difficulty: 'Avanzado',
      xpReward: 500,
      locked: true,
      progress: 0
    }
  ]

  const achievements = [
    {
      id: 'first_lesson',
      title: 'Primer Paso',
      description: 'Completa tu primera lección',
      icon: Award,
      unlocked: false,
      xp: 50
    },
    {
      id: 'dork_master',
      title: 'Maestro de Dorks',
      description: 'Crea 10 Google Dorks exitosos',
      icon: Search,
      unlocked: false,
      xp: 200
    },
    {
      id: 'detective_pro',
      title: 'Detective Profesional',
      description: 'Resuelve todos los casos del juego',
      icon: Trophy,
      unlocked: false,
      xp: 1000
    }
  ]

  const calculateLevel = (xp) => {
    return Math.floor(xp / 1000) + 1
  }

  const getNextLevelXP = (currentXP) => {
    const currentLevel = calculateLevel(currentXP)
    return currentLevel * 1000
  }

  const handleModuleClick = (module) => {
    if (module.locked) {
      return
    }

    switch(module.id) {
      case 'intro':
        navigate('/academy/lesson/intro')
        break
      case 'tools':
        navigate('/academy/lesson/tools')
        break
      case 'techniques':
        navigate('/academy/dork-simulator')
        break
      case 'mindmap':
        navigate('/academy/mindmap')
        break
      case 'detective':
        navigate('/academy/detective-game')
        break
      default:
        break
    }
  }

  return (
    <div className="academy-dashboard">
      <div className="academy-header">
        <h1>Academia OSINT</h1>
        <p>Aprende las técnicas de inteligencia de fuentes abiertas de forma interactiva</p>
      </div>

      {/* Barra de progreso del usuario */}
      <div className="user-progress-section">
        <div className="progress-card">
          <div className="progress-header">
            <div className="level-info">
              <span className="level-badge">Nivel {userProgress.level}</span>
              <span className="xp-text">{userProgress.xp} XP</span>
            </div>
            <div className="streak-info">
              <Clock size={16} />
              <span>{userProgress.currentStreak} días de racha</span>
            </div>
          </div>
          <div className="xp-progress-bar">
            <div 
              className="xp-progress-fill"
              style={{ 
                width: `${(userProgress.xp % 1000) / 10}%` 
              }}
            />
          </div>
          <p className="xp-next-level">
            {1000 - (userProgress.xp % 1000)} XP para el siguiente nivel
          </p>
        </div>
      </div>

      {/* Módulos de aprendizaje */}
      <div className="modules-section">
        <h2>Módulos de Aprendizaje</h2>
        <div className="modules-grid">
          {modules.map((module) => (
            <div 
              key={module.id}
              className={`module-card ${module.locked ? 'locked' : ''}`}
              onClick={() => handleModuleClick(module)}
            >
              <div className="module-header">
                <div className="module-icon">
                  <module.icon size={24} />
                </div>
                {module.locked ? <Lock size={16} /> : <Unlock size={16} />}
              </div>
              
              <h3>{module.title}</h3>
              <p>{module.description}</p>
              
              <div className="module-meta">
                <span className="duration">
                  <Clock size={14} />
                  {module.duration}
                </span>
                <span className={`difficulty ${module.difficulty.toLowerCase()}`}>
                  {module.difficulty}
                </span>
              </div>
              
              <div className="module-footer">
                <div className="xp-reward">
                  <TrendingUp size={14} />
                  <span>{module.xpReward} XP</span>
                </div>
                {module.progress > 0 && (
                  <div className="module-progress">
                    <div 
                      className="module-progress-fill"
                      style={{ width: `${module.progress}%` }}
                    />
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Logros */}
      <div className="achievements-section">
        <h2>Logros</h2>
        <div className="achievements-grid">
          {achievements.map((achievement) => (
            <div 
              key={achievement.id}
              className={`achievement-card ${achievement.unlocked ? 'unlocked' : ''}`}
            >
              <div className="achievement-icon">
                <achievement.icon size={32} />
              </div>
              <h4>{achievement.title}</h4>
              <p>{achievement.description}</p>
              <span className="achievement-xp">+{achievement.xp} XP</span>
            </div>
          ))}
        </div>
      </div>

      {/* Estadísticas rápidas */}
      <div className="quick-stats">
        <div className="stat-card">
          <h4>Lecciones Completadas</h4>
          <p className="stat-number">{userProgress.completedLessons.length}</p>
        </div>
        <div className="stat-card">
          <h4>Tiempo Total</h4>
          <p className="stat-number">0h</p>
        </div>
        <div className="stat-card">
          <h4>Casos Resueltos</h4>
          <p className="stat-number">0</p>
        </div>
      </div>
    </div>
  )
}

export default AcademyDashboard